@charset "UTF-8";
%close-style {
    right: 32px;
    top: 30px;
    .layui-layer-ico {
        @extend .icons-close;
        margin: 10px 10px 0 0;
    }
}

.layui-layer {
    border-radius: 4px!important;
    &.del-demo-class {
        // padding-bottom: 10px;
        // height: 220px !important;
        .layui-layer-content {
            text-align: center;
            height: 130px!important; // line-height: 154px;
            .close-text {
                padding-top: 48px;
                color: $font-color;
                font-size: 18px;
            }
        }
        .layui-layer-btn {
            text-align: center;
            a {
                width: 120px;
                height: 40px;
                line-height: 40px;
                border: none;
                font-size: 16px;
                &.layui-layer-btn0 {
                    background: #f18181;
                }
                &.layui-layer-btn1 {
                    background: #e7e7e8;
                    color: #515151;
                }
            }
        }
        .layui-layer-setwin {
            @extend %close-style;
        }
    }
    &.edit-demo-class {
        .layui-layer-content {
            padding: 0 0;
            iframe {
                padding: 20px 40px; // padding-top: 120px;
            }
        }
        .layui-layer-setwin {
            @extend %close-style;
        }
    }
    &.service-iframe-class {
        .layui-layer-content {
            padding: 0 0;
            iframe {
                padding: 20px 50px; // padding-top: 120px;
            }
        }
        .layui-layer-setwin {
            @extend %close-style;
        }
    }
}

.task-popup {
    padding: 25px 10px;
    font-size: 18px;
    font-weight: 300;
}

.backlog {
    position: relative;
    text-align: center;
    &:before {
        position: absolute;
        bottom: 1px;
        left: 14.5px;
        content: "";
        width: 4px;
        height: 4px;
        background-color: $theme-blue;
        border-radius: 4px;
    }
}

.laydate_click {
    &.backlog {
        @extend .backlog;
        &:before {
            background-color: #fff;
        }
    }
}

.laydate_nothis {
    &.backlog {
        @extend .backlog;
        &:before {
            background-color: #caced3;
        }
    }
}