@charset "UTF-8";
$bdc: #eee;
$amount:10%;
$pub-bd: 1px solid $bdc;
$warning: #f18181;
$warning-btn: #ed7679;
$warning-btn-hover: #f39496;
$warning-btn-active: #ed7679;
$warning-btn-hover: #f39496;

$gray-btn:#e4e4e5;
$gray-btn-hover:#f0f1f4;


$font-color:#4a4a4a;
$theme-blue:#4882bf;
$theme-blue-active:#4379b6;
$theme-blue-hover:#548ccc;
$theme-blue-disabled:#e4e4e5;


%text-ellipsis {
    // 文本一行隐藏
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.text-overflow {
    @extend %text-ellipsis;
}

%pd-lr {
    padding: 0 0.133333rem;
}

@mixin multi-row($line) {
    //设置文本多行超出隐藏
    display: -webkit-box;
    display: box;
    overflow: hidden;
    // font-size: inherit;
    -webkit-line-clamp: $line;
    line-clamp: $line;
    -webkit-box-orient: vertical;
    box-orient: vertical;
    text-align: justify;
}
