@charset "UTF-8";
.czmm {
    width: 100%;
    min-height: 609px;
    background-color: #FFF;
    padding-top: 10px;
    box-shadow: 0 1px 4px 0 rgba(190, 190, 190, 0.5);
    @extend %clearfix;
}

.czmm_title {
    font-size: 20px;
    font-weight: 300;
    color: $font-color;
    text-align: center;
    margin-top: 40px;
}

.czmm_title2 {
    color: #808489;
    font-size: 14px;
    text-align: center;
    line-height: 58px;
}

.czmm_hr {
    display: block;
    width: 448px;
    height: 30px;
    margin-left: auto;
    margin-right: auto;
    border-bottom: 1px solid #d8d8d8;
}

.set-luyin {
    font-size: 16px;
    color: $font-color;
}

@mixin arrow($rt) {
    .select-menu {
        width: 100%;
        font-size: inherit;
        background: #fff url(../images/triangle.png) no-repeat $rt center;
        &:focus {
            background: #fff url(../images/triangle-hover.png) no-repeat $rt center;
        }
    }
}

.input-pub {
    border: 1px solid #caced3;
    height: 36px;
    padding: 0 5px;
    line-height: 34px;
    // font-size: 16px;
    border-radius: 2px;
    background-color: #ffffff;
    color: $font-color;
    &:disabled {
        background-color: #f2f4f5;
        color: #f2f4f5;
    }
    &:focus {
        border: 1px solid #548ccc;
        box-shadow: 0 0 2px 0 #adcbeb;
        &:hover {
            border: 1px solid #548ccc;
            box-shadow: 0 0 2px 0 #adcbeb;
        }
    }
    &:hover {
        border: 1px solid #a8b1ba;
        box-shadow: 0 0 2px 0 #b0b8c1;
        // outline: 1px solid  #b0b8c1;
    }
    &.warning {
        border: 1px solid #f18181;
    }
    &.username {
        width: 320px;
    }
    &.checkcode {
        width: 200px;
        float: left;
        margin-right: 4px;
    }
    &.theme {
        width: 400px;
    }
    &.xl-size {
        width: 230px;
    }
    &.l-size {
        width: 160px;
    }
    &.m-size {
        width: 130px;
    }
    &.s-size {
        width: 100px;
    }
}

.drop-list-box {
    display: inline-block;
    float: left;
    @extend %clearfix;
    position: relative;
    font-size: 16px;
    // height: 36px;
    &.service {
        float: left;
        height: 28px;
        width: 168px;
        font-size: 14px;
        @include arrow(168px - 22px);
        &:before {
            top: 12px;
        }
        &.user {
            width: 108px;
            @include arrow(108px - 22px);
        }
        input {
            height: 28px;
            // font-size: 14px;
            line-height: 26px;
        }
        .task-time {
            top: 29px;
        }
    }
    &.username {
        width: 320px;
    }
    &.checkcode {
        width: 200px;
    }
    &.theme {
        width: 400px;
        @include arrow(400px - 25px);
    }
    &.xl-size {
        width: 230px;
    }
    &.l-size {
        width: 160px;
        @include arrow(160px - 25px);
    }
    &.m-size {
        width: 130px;
    }
    &.s-size {
        width: 100px;
    }
    &.layer-drop {
        width: 80px;
        margin-right: 10px;
        font-size: 13px;
        @include arrow(80px - 18px);
        &:before {
            right: 6px;
            top: 13px;
        }
        .task-time {
            top: -82px;
            // top: 30px;
        }
    }
    .select-menu {
        width: 100%;
        font-size: inherit;
        &.time {
            text-align: center;
        }
    }
    .task-time {
        width: 100%;
        font-size: inherit;
    }
    .time-name {
        position: absolute;
        left: 10px;
        top: 10px;
        font-size: inherit;
        line-height: 100%;
    }
}

.codeimg {
    display: inline-block;
    width: 116px;
    height: 36px;
    background-color: #90c95b;
    color: #FFF;
    line-height: 36px;
    text-align: center;
    font-size: 16px;
}

.fasong {
    display: inline-block;
    width: 116px;
    height: 36px;
    background-color: #caced3;
    color: #FFF;
    line-height: 36px;
    text-align: center;
    font-size: 16px;
}

.message {
    display: inline-block;
    margin-left: 10px;
    font-size: 14px;
    font-weight: 300;
    color: #eb6a6e;
}

.czmm_text2 {
    text-align: center;
    font-size: 14px;
    font-weight: 300;
    text-align: center;
    color: $font-color;
    margin-top: 22px;
}

.czmm_text2 a {
    color: #3a6db1;
    text-decoration: underline;
}


