@charset "UTF-8";
.icons {
    display: inline-block;
    vertical-align: middle;
}

button {
    line-height: 1;
}

.loginbody {
    position: absolute;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    background: url(../images/bg.png) no-repeat 50% 50%;
    background-size: cover;
}

.login-logo {
    text-align: center;
    a {
        width: 142px;
        height: 69px;
        display: inline-block;
    }
}

.date-control {
    .icons {
        cursor: pointer;
        vertical-align: middle;
        float: left;
        margin-top: 8px;
    }
    .date-item {
        float: left;
        margin: 0 10px;
    }
}

.login-box {
    width: 360px;
    position: fixed;
    @extend %clearfix;
    margin: 0 auto;
    top: 50%;
    margin-top: -235px;
    left: 50%;
    margin-left: -180px;
}

.login-form {
    width: 360px;
    @extend %clearfix;
    padding: 10px 0 35px 0;
    border-radius: 2px;
    margin: 0 auto;
    margin-top: 27px;
    background-color: #ffffff;
    box-shadow: 0 20px 30px 0 rgba(0, 0, 0, 0.6);
}

.warning-login {
    width: 250px;
    margin: 0 auto;
    height: 35px;
    line-height: 35px;
    margin-bottom: -20px;
    color: $warning;
    font-size: 14px;
}

// .login-form {
//     width: 100%;
//     @extend %clearfix;
//     // padding-top: 30px;
// }
.input-group {
    width: 250px; // height: 42px;
    margin: 20px auto;
    position: relative;
    @extend %clearfix;
    &.fg-pwd {
        margin-top: 30px;
        .rem-pwd-item {
            cursor: pointer;
            @extend %clearfix;
            line-height: 14px;
        }
        .check-pwd {
            display: none;
        }
        .icons {
            position: static;
            // margin-right: 2px;
            width: 17px;
            height: 14px;
            float: left;
            margin: 0 5px 0 0;
            // overflow: hidden;
        }
        // .check-pwd:checked + .icons {
        //     @extend .icons-check-true;
        // }
    }
    .input-control {
        width: 100%;
        line-height: 41px;
        height: 42px;
        border: none;
        padding-left: 35px;
        border-bottom: 1px solid #d0d0d0;
        &:focus {
            color: $theme-blue;
            border-bottom: none;
            border-bottom: 1px solid $theme-blue; // line-height:41px;
            box-shadow: 0 1px 0 0 $theme-blue;
        }
    }
    .icons {
        position: absolute;
        top: 12px;
        &.user {
            left: 0;
        }
        &.del {
            right: 0;
            cursor: pointer;
        }
        &.eye {
            top: 15px;
            right: 30px;
        }
    }
}

.rem-pwd-item {
    color: $theme-blue;
    float: left;
    font-size: 12px;
    input {
        vertical-align: middle;
        margin-right: 5px;
    }
}

.fgt-pwd {
    color: $theme-blue;
    float: right;
    font-size: 12px;
    line-height: 1;
    &:hover {
        color: $theme-blue-hover;
    }
}

.er-coder {
    width: 100px;
    height: 40px;
    background-color: #90c95a;
    color: #fff;
    font-size: 14px; // line-height: 1;
    text-align: center;
    line-height: 40px;
    display: inline-block;
    position: absolute;
    letter-spacing: 5px;
    right: 0;
    bottom: 1px;
    cursor: pointer;
}

.copyright {
    text-align: center;
    width: 100%;
    position: absolute;
    bottom: 16px;
    color: #fff;
    font-size: 14px;
    color: $font-color;
}

.form-fgpw {
    width: 100%;
    .form-box {
        width: 100%;
        margin: 30px auto;
        padding-left: 340px;
        @extend %clearfix;
        &.add-task {
            padding-left: 0; // padding-bottom: 20px;
        }
        h4 {
            text-align: center;
            font-size: 20px;
            font-weight: 300;
            color: $font-color;
        }
        .input-bt-line {
            width: 481px;
            margin: 0 auto;
            border-bottom: 1px solid #d8d8d8;
            &.last {
                border-bottom: 0;
            }
            &.button {
                border-bottom: 0;
            }
        }
    }
}

.input-block {
    width: 100%;
    margin: 20px auto;
    @extend %clearfix;
    position: relative;
    .control-btn {
        position: absolute;
        top: 5px;
        &.reduce-btn {
            right: -35px;
        }
        &.add-more {
            right: -70px;
        }
        &.add-btn {
            right: -35px;
        }
    }
    &.task {
        @extend %clearfix;
        height: 36px;
        margin: 25px auto;
        .input-pub {
            float: left;
            margin-right: 10px;
            &.last {
                margin-right: 0;
            }
        }
        .message {
            position: absolute;
            margin-top: 10px;
            width: 50%;
        }
        &.call-tel {
            height: 30px;
            margin: 5px auto;
            .td-control-call {
                position: absolute;
                left: 458px;
            }
            input {
                height: 30px;
                line-height: 28px;
                &.s-size {
                    @include placehoder {
                        font-size: 14px;
                    }
                }
            }
            .tel-group {
                height: 30px;
                .message {
                    top: -32px;
                    width: 100%;
                    left: 43px;
                    margin-top: 0;
                    font-size: 12px;
                }
                input {
                    height: 22px;
                    line-height: 22px;
                    @include placehoder {
                        font-size: 14px;
                    }
                }
            }
        }
    }
    &.btn-row {
        // margin-top: 40px;
        margin: 40px auto;
    }
    .input-label {
        line-height: 36px;
        font-size: 16px;
        color: $font-color;
        width: 80px;
        float: left;
        display: inline-block;
    }
}

.tel-group {
    @extend .input-pub;
    float: left;
    width: 230px;
    padding: 3px !important;
    display: block;
    position: relative;
    input {
        border: none;
        float: left;
        height: 28px;
        line-height: 28px;
        display: inline-block;
        border-right: 1px solid #caced3;
        padding-left: 8px;
    }
    .base {
        width: 50px;
    }
    .num {
        width: 60px;
    }
    .phone {
        width: 110px;
        border-right: 0;
    }
}

.set-luyin {
    cursor: pointer;
    @extend %clearfix;
    line-height: 20px;
    .check-box {
        display: none;
    }
    .icons {
        margin: 0 10px 0 0;
        vertical-align: sub;
        float: left;
    }
    // input:checked + .icons {
    //     @extend .icons-checkbox;
    // }
}