@charset "UTF-8";
// 登录按钮
.login-btn {
    width: 250px;
    height: 50px;
    border-radius: 2px;
    color: #fff;
    font-size: 16px;
    border: none;
    background-color: $theme-blue;
    // box-shadow: 0 8px 16px 0 rgba(72, 130, 191, 0.4);
    box-shadow: 0 14px 30px 0 rgba(72, 130, 191, 0.4);
    outline: none;
    margin-top: 20px;
    &:hover {
        background-color: $theme-blue-hover;
    }
}

// 找回密码相关
.czmm_button {
    border: none;
    width: 180px;
    height: 40px;
    border-radius: 2px;
    background-color: $theme-blue;
    font-size: 16px;
    color: #FFF;
    margin-left: 78px;
    &.cancle {
        width: 120px;
        color: #636363;
        background-color: $gray-btn;
        margin-left: 20px;
        &:hover {
            background-color: $gray-btn-hover;
        }
    }
    &.disabled {
        background-color: #e7e8e8;
        color: #fff;
    }
    &:active {
        background-color: $theme-blue-active;
    }
    &:hover {
        background-color: $theme-blue-hover;
    }
}

// table中的操作按钮
.td-btn {
    display: inline-block;
    float: left;
    cursor: pointer;
    margin-left: 10px;
    font-size: 14px;
    width: 50px;
    // height: 28px;
    line-height: 26px;
    color: $font-color;
    border-radius: 2px;
    background-color: #ffffff;
    border: 1px solid #cacfd3;
   
    &:active {
        color: $font-color;
        background-color: #f2f4f5;
    }
     &:focus {
        background-color: #f2f4f5;
        border: solid 1px #808489;
    }
    &:hover {
        color: $font-color;
        border: solid 1px #808489;
    }
    &.disabled {
        background-color: #f2f4f5;
        border: solid 1px #e7e8e8;
        color: #cacfd3;
    }
    text-align: center;
    &.get-task {
        width: 60px;
        height: 30px;
        line-height: 30px;
        margin-left: -5px;
    }
    &.service {
        // float: none;
        width: auto;
        padding: 0 10px;
        margin-left: 8px;
        // vertical-align: top;
        width: 80px;
        &.send {
            background-color: $theme-blue;
            color: #fff;
            border: 1px solid $theme-blue;
            &:active {
                background-color: $theme-blue-active;
            }
            &:hover {
                background-color: $theme-blue-hover;
            }
        }
        // color:$font-color;
    }
    &.call {
        background-color: $theme-blue;
        border: 1px solid $theme-blue;
        color: #fff;
        margin-left: 0;
        &:hover {
            background: lighten($theme-blue, 10%);
            border: 1px solid lighten($theme-blue, 10%);
            color: #fff;
        }
    }
}

.sm-btn {
    width: 130px;
    height: 40px;
    line-height: 40px;
    border-radius: 2px;
    background-color: $theme-blue;
    text-align: center;
    font-size: 14px;
    color: #fff;
    display: inline-block;
    &:active {
        color: #fff;
        background-color: $theme-blue-active;
    }
    &:hover {
        color: #fff;
        background-color: $theme-blue-hover;
    }
}
