@charset "UTF-8";
.footer {
    height: 90px;
    /* line-height: 90px; */
    color: #909399;
    font-size: 14px;
    text-align: center;
    // position: absolute;
    margin: 0 auto;
    width: 100%;
    line-height: 100%;
    // bottom: 0;
    &.user {
        margin-top: 45px;
    }
}

.warning-box {
    position: absolute;
    bottom: 10px;
    left: 0;
    width: 100%;
    height: 46px;
    line-height: 46px;
    border-radius: 4px;
    background-color: #f18181;
    font-size: 14px;
    color: #fff;
    padding: 0 15px;
    .icons {
        vertical-align: middle;
        margin-right: 10px;
    }
    strong {
        font-size: 18px;
        font-weight: 300;
    }
}