@charset "UTF-8";
.warning-hint{
	display: none;
	color:$warning;
	font-size: 14px;
	margin-left: 10px;
	.check-item{
		margin-left: 10px;
		&.yes{
			color:$warning;
			&:hover{
				color:lighten($warning, $amount);
			}
		}
		&.cancle{
			color:$font-color;
			&:hover{
				color:lighten($font-color, $amount);
			}
		}
		span{
			vertical-align: middle;
			margin-right: 5px;
		}
	}
}