@charset "UTF-8";
.pub-table {
    width: 100%;
    background-color: #fff;
    box-shadow: 0 1px 4px 0 rgba(190, 190, 190, 0.5);
    margin: 0 auto;
    thead {
        width: 100%;
        tr {
            height: 60px;
            background-color: #e8edf3;
            border: solid 1px #cdd9ea;
            padding: 0 30px;
            color: $font-color;
            font-size: 16px;
        }
    }
    tbody {
        width: 100%;
        tr {
            height: 40px;
            &:nth-child(even) {
                background-color: #f2f4f5;
            }
            &:nth-child(odd) {
                background-color: #fff;
            }
            &:hover {
                background-color: #e7eaf0;
            }
            td {
                font-size: 14px;
                height: 40px;
                line-height: 40px;
                font-weight: 300;
                color: $font-color;
            }
            .online {
                color: #499600;
            }
            .offline {
                color: #eb6a6e;
            }
        }
    }
    .item-title {
        cursor: pointer;
    }
    td {
        &:first-child {
            padding-left: 30px
        }
        &:last-child {
            padding-right: 30px
        }
    }
    &.iframe {
        width: 99.5%;
        outline: 1px solid #f5f5f5;
        &.service {
            tbody {
                height: 160px;
            }
        }
        tr {
            width: 100%;
            display: table;
        }
        tbody {
            width: 100%;
            height: 160px;
            overflow-y: auto;
            overflow-x: hidden;
            display: block;
            position: relative;
        }
        td {
            &.last {
                padding-right: 0!important;
            }
            &:first-child {
                padding-left: 20px;
            }
            &:last-child {
                padding-right: 0;
            }
        }
    }
    .td-link {
        color: $theme-blue;
        text-decoration: underline;
        &:hover {
            color: darken($theme-blue, 20%);
            text-decoration: none;
        }
    }
}

.td-control {
    // float: right;
    @extend %clearfix;
    margin-top: 5px;
    display: none;
    &.call {
        float: none;
    }
}

.even {
    background-color: #fff;
}

.odd {
    background-color: #f2f4f5;
}

.hover {
    background-color: #e7eaf0;
}

%arrow {
    bottom: 100%;
    right: 50px;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
}

.drop-menu {
    width: 100px;
    position: relative;
    // z-index: 100;
    left: 0;
    box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
    border: solid 1px #b9c0c8;
    background-color: #fff;
    li {
        text-align: center;
        line-height: 35px;
        font-size: 14px;
        a {
            color: $font-color;
            display: block;
            &:hover {
                color: #333;
                background-color: #f9f9f9;
            }
        }
    }
    &:after {
        @extend %arrow;
        right: 10px;
        border-color: rgba(255, 255, 255, 0);
        border-bottom-color: #fff;
        border-width: 8px;
        margin-right: 1px;
    }
    &:before {
        @extend %arrow;
        right: 10px;
        border-color: rgba(185, 192, 200, 0);
        border-bottom-color: #b9c0c8;
        border-width: 9px;
        margin-left: -9px;
    }
}

.record-box {
    position: relative;
    width: 100%;
    .record {
        margin-left: 5px;
    }
    .tips-text {
        position: absolute;
        font-size: 12px;
        color: $font-color;
        margin-left: 10px;
        display: none;
    }
    .td-tips {
        display: none;
        position: absolute;
        top: 46px;
        z-index: 10;
        left: -182px;
        width: 260px;
        height: 90px;
        padding: 18px 15px;
        border-radius: 4px;
        background-color: #ffffff;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
        border: solid 1px #b9c0c8;
    }
    .send-record {
        width: 100%;
        background: #fff;
        // position: relative;
        &:after {
            @extend %arrow;
            border-color: rgba(255, 255, 255, 0);
            border-bottom-color: #fff;
            border-width: 10px;
            margin-right: 1px;
        }
        &:before {
            @extend %arrow;
            border-color: rgba(185, 192, 200, 0);
            border-bottom-color: #b9c0c8;
            border-width: 11px;
            margin-left: -11px;
        }
        p {
            line-height: 100%;
            font-size: 14px;
            margin-bottom: 8px;
        }
        .td-input-block {
            @extend %clearfix;
        }
        %pub {
            height: 30px;
            border-radius: 2px;
            background-color: #ffffff;
            border: solid 1px #c5c9cd;
            float: left;
        }
        .td-email {
            width: 170px;
            @extend %pub;
            padding: 5px;
            margin-right: 5px;
        }
        .td-send-btn {
            width: 50px;
            font-size: 14px;
            @extend %pub;
        }
    }
}

.record {
    position: relative;
}

.word {
    margin-left: 8px;
}

.main-tag-box {
    width: 100%;
    @extend %clearfix;
    margin-top: 30px;
    line-height: 30px;
    position: relative;
    .sm-btn-box {
        float: right;
    }
}

.task-status {
    width: 300px;
    line-height: 25px;
    margin-bottom: 16px;
    font-size: 16px;
    color: $font-color;
}

.content-main {
    width: 100%;
    position: relative;
    background-color: #fff;
    min-height: 661px;
    z-index: 5;
    box-shadow: 0 1px 4px 0 rgba(190, 190, 190, 0.5);
}

.tab-wrap {
    // display: none;
    float: left;
    position: relative;
    height: 42px;
    margin-bottom: -1px;
    margin-left: 5px;
    @extend %clearfix;
    li {
        position: absolute;
        bottom: 0;
        line-height: 42px;
        text-indent: 1em;
        font-size: 16px;
        letter-spacing: -1px;
        cursor: pointer;
        @extend .icons-tabcur;
        color: $font-color;
        &:hover {
            color: #333;
        }
        &.first {
            left: 0;
            z-index: 2;
        }
        &.last {
            left: 136px;
        }
        &.current {
            z-index: 3;
            @extend .icons-tabcur-active;
            &:hover {
                @extend .icons-tabcur-active;
            }
        }
    }
}

.date-control {
    position: absolute;
    bottom: 10px;
    left: 410px;
    .date-item {
        width: 140px;
        height: 32px;
        line-height: 30px;
        border-radius: 2px;
        text-align: center;
        font-size: 16px;
        color: #333;
        background-color: #ffffff;
        border: solid 1px #e7e8e8;
    }
}

.sm-btn-box {
    position: absolute;
    right: 0;
    bottom: 10px;
}

.table-box {
    display: none;
    width: 100%;
    background: #fff;
    min-height: 150px;
    &.show {
        display: block;
    }
    &.service {
        min-height: 701px;
    }
}

.no-task {
    width: 100%;
    text-align: center;
    color: #808489;
    font-size: 16px;
    padding: 190px 0;
    span {
        display: block;
        margin: 20px auto;
    }
}

.task-title {
    color: $font-color;
    font-size: 18px;
    // padding-top: 20px
}

.task-info {
    margin: 20px 0;
    font-size: 14px;
    color: $font-color;
    line-height: 1;
    p {
        margin: 10px 0;
    }
    .item {
        margin: 10px 0;
        @extend .clearfix;
        >span {
            float: left;
            padding-top: 7px;
        }
    }
}

.box-fixed {
    // position: fixed;
    width: 100%;
    // top: -20px;
    // left: 0;
    background: #fff;
    &.service {
        top: 0;
        .task-title {
            font-size: 14px;
            color: #6c6c6c;
            padding-top: 20px;
        }
        .task-info {
            font-size: 14px;
            color: #6c6c6c;
            margin-top: 10px;
        }
    }
}

.iframe-body {
    background-color: #fff;
    padding-top: 0;
    border-radius: 4px;
    &.service {
        // padding: 20px 0 0 0;
    }
}

.pub-toast {
    padding: 0 30px;
    font-size: 16px;
    .sucess-icons {
        display: block;
        margin: 20px auto;
    }
}

.task-time {
    display: none;
    border: 1px solid #caced3;
    z-index: 30;
    padding: 5px 0;
    font-size: inherit;
    border-radius: 2px;
    background-color: #ffffff;
    width: inherit;
    position: absolute;
    right: 0;
    top: 37px;
    li {
        line-height: 35px;
        cursor: pointer;
        padding-left: 10px;
        color: $font-color;
        &:hover {
            background-color: #f5f5f5;
        }
    }
}

#call-user,
.td-call-box {
    display: none;
}

.call-other-box {
    width: 100%;
    background-color: #fff;
    .call-other {
        text-indent: 20px;
    }
}

.call-other {
    color: $theme-blue;
    display: block;
    line-height: 40px;
    &:active,
    &:hover {
        color: $theme-blue-hover;
        // background-color: #e7eaf0;
        .icons {
            @extend .icons-add-btn-active;
        }
    }
}

.task-theme {
    cursor: pointer;
}